.grid {
    display: grid;
    grid-template-columns: 34rem auto;
    grid-gap: 1rem;
    line-height: 1.2em;
}
.input-container {
    display: grid;
    grid-template-columns: auto 6rem 4rem;
    grid-gap: 1rem;
    margin: 1rem;
}

.input-amount {
    display: inline-block;
    width: 80px;
}
.food-list {
    margin: 1rem;
    font-size: 0.9rem;
    line-height: 1.2em;
}
.food {
    cursor: pointer;
    border: 1px solid white;
}
.food__delete {
    display: none;
    margin-left: 1rem;
    cursor: pointer;
}
.food--selected {
    background-color: lightgrey;
    border-color: lightgrey;
}
.food-list-actions {
    display: flex;
    justify-content: space-between;
    margin: 1rem;
}
.nutrient-table {
    font-size: 0.8rem;
}

.nutrient-table__section {
    border-top: 1px solid black;
}

.nutrient-table__section-toggle {
    cursor: pointer;
    width: 26px;
}

.nutrient-table__column--toggle {
    width: 2rem;
}

.nutrient-table__column--name {
    width: 12rem;
}

.nutrient-table__column--amount {
    width: 14rem;
}

.nutrient-table__column--max {
    width: 4rem;
}

.nutrient-table__column--unit {
    width: 4rem;
}

.nutrient-table__column--no-padding {
    padding: 0;
}

.nutrient-table__column--received {
    text-align: right;
}

.nutrient-table__column--recommended {
    width: 70px;
}
.bar {
    display: flex;
    border: 1px solid black;
    height: 1rem;
    width: 100px;
}

.bar__fill {
    background-color: black;
    height: 100%;
    width: 0%;
}

.bar__fill--selected {
    background-color: red;
}
.date-picker {
    margin: 1rem;
}

.selected-date {
    margin: 1rem;
}
